/* ----------------------- Common desktop header ----------------------- */

.header {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  z-index: 999;
  padding: clamp(13px, calc(13px + 37.00 * ((100vw - 768px) / 672)), 50px) 0;
  display: flex;
  align-items: center;
  transition: all 0.3s ease-in-out;
}

.header.absolute {
    position: absolute;
  }

.header.fixed {
    position: fixed;
  }

.header:before {
    position: absolute;
    inset: 0;
    content: '';
    z-index: -1;
    background-color: transparent;
    opacity: 0;
    transition: all 0.25s ease;
  }

.header.scrolled:not(.open-menu) {
      background: rgba(var(--cl-grey), 0.8);
      -webkit-backdrop-filter: blur(16px);
